export const idlFactory = ({ IDL }) => {
  return IDL.Service({
    'addinCompletedList' : IDL.Func([IDL.Text], [], []),
    'addinPendingList' : IDL.Func([IDL.Text], [], []),
    'getCompletedtodo' : IDL.Func([], [IDL.Vec(IDL.Text)], []),
    'getCurrentUser' : IDL.Func([], [IDL.Text], []),
    'getPendingtodo' : IDL.Func([], [IDL.Vec(IDL.Text)], []),
    'isCorrectPassword' : IDL.Func([IDL.Text], [IDL.Text], ['query']),
    'isUsernameTaken' : IDL.Func([IDL.Text], [IDL.Bool], ['query']),
    'registerUser' : IDL.Func([IDL.Text, IDL.Text], [IDL.Bool], []),
    'setCurrentUser' : IDL.Func([IDL.Text], [], []),
  });
};
export const init = ({ IDL }) => { return []; };
